import React from 'react';                 //importing react library


class Home extends React.Component {       // making a class base components 

    render() {                             // render prop takes a function that returns a React Home element and calls it instead of implementing its own render logic. 
        return <p>Hey you there!</p>       // return the html element to view in home ccompoents          
    }
}

export default Home;                      // export the home compoents      